#color-picker-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    align-items: center;
    gap: 1rem;
    flex-direction: row;
    width: 450px;
    border-radius: var(--border-radius-2)
}

#light #color-picker-wrapper {
    background: var(--color-light);
    border: 3px solid black;
}

#dark #color-picker-wrapper {
    background: var(--color-primary-nav-item-bg);
    border: 3px solid white;
}

#hue-numbers-disp {
    flex-basis: 30px;
}

#color-picker {
    flex-basis: 175px;
}

#change-btn {
    flex-basis: 175px;
}

#light input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    background-color: var(--color-black);
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    border: 2px solid white;
}

#dark input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    background-color: var(--color-white);
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    border: 2px solid black;
}

input[type="range"]::-webkit-slider-runnable-track{
    background: linear-gradient(to right, hsla(0, 100%, 50%, 1),hsla(10, 100%, 50%, 1),hsla(20, 100%, 50%, 1),hsla(30, 100%, 50%, 1),hsla(40, 100%, 50%, 1),hsla(50, 100%, 50%, 1),hsla(60, 100%, 50%, 1),hsla(70, 100%, 50%, 1),hsla(80, 100%, 50%, 1),hsla(90, 100%, 50%, 1),hsla(100, 100%, 50%, 1),hsla(110, 100%, 50%, 1),hsla(120, 100%, 50%, 1),hsla(130, 100%, 50%, 1),hsla(140, 100%, 50%, 1),hsla(150, 100%, 50%, 1),hsla(160, 100%, 50%, 1),hsla(170, 100%, 50%, 1),hsla(180, 100%, 50%, 1),hsla(190, 100%, 50%, 1),hsla(200, 100%, 50%, 1),hsla(210, 100%, 50%, 1),hsla(220, 100%, 50%, 1),hsla(230, 100%, 50%, 1),hsla(240, 100%, 50%, 1),hsla(250, 100%, 50%, 1),hsla(260, 100%, 50%, 1),hsla(270, 100%, 50%, 1),hsla(280, 100%, 50%, 1),hsla(290, 100%, 50%, 1),hsla(300, 100%, 50%, 1),hsla(310, 100%, 50%, 1),hsla(320, 100%, 50%, 1),hsla(330, 100%, 50%, 1),hsla(340, 100%, 50%, 1),hsla(350, 100%, 50%, 1),hsla(360, 100%, 50%, 1));
    border: none;
    border-radius: 5px;
}

input[type="range"]::-moz-range-thumb {
    border: none; /*Removes extra border that FF applies*/
    border-radius: 0; /*Removes default border-radius that FF applies*/
    background-color: var(--color-black);
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    border: 2px solid white;
}
input[type="range"]::-moz-range-track {
    background: -moz-linear-gradient(right, hsla(0, 100%, 50%, 1),hsla(10, 100%, 50%, 1),hsla(20, 100%, 50%, 1),hsla(30, 100%, 50%, 1),hsla(40, 100%, 50%, 1),hsla(50, 100%, 50%, 1),hsla(60, 100%, 50%, 1),hsla(70, 100%, 50%, 1),hsla(80, 100%, 50%, 1),hsla(90, 100%, 50%, 1),hsla(100, 100%, 50%, 1),hsla(110, 100%, 50%, 1),hsla(120, 100%, 50%, 1),hsla(130, 100%, 50%, 1),hsla(140, 100%, 50%, 1),hsla(150, 100%, 50%, 1),hsla(160, 100%, 50%, 1),hsla(170, 100%, 50%, 1),hsla(180, 100%, 50%, 1),hsla(190, 100%, 50%, 1),hsla(200, 100%, 50%, 1),hsla(210, 100%, 50%, 1),hsla(220, 100%, 50%, 1),hsla(230, 100%, 50%, 1),hsla(240, 100%, 50%, 1),hsla(250, 100%, 50%, 1),hsla(260, 100%, 50%, 1),hsla(270, 100%, 50%, 1),hsla(280, 100%, 50%, 1),hsla(290, 100%, 50%, 1),hsla(300, 100%, 50%, 1),hsla(310, 100%, 50%, 1),hsla(320, 100%, 50%, 1),hsla(330, 100%, 50%, 1),hsla(340, 100%, 50%, 1),hsla(350, 100%, 50%, 1),hsla(360, 100%, 50%, 1));
    border: none;
    border-radius: 5px;
}
#color-picker {
    padding: 0;
    background: transparent;
    -webkit-appearance: none;
    cursor: pointer;
}



@media screen and (max-width: 680px) {
    #color-picker-wrapper {
        display: flex;
        justify-content: space-between;
        padding: 1rem;
        align-items: center;
        gap: 1rem;
        flex-direction: column;
        width: 200px;
        height: 150px;
    }


    #hue-numbers-disp {
        flex-basis: auto;
    }

    #color-picker {
        flex-basis: auto;
    }

    #change-btn {
        flex-basis: auto;
    }
}